import React from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll'

import '../Styles/App.css'
import Avatar from '../images/myAvatar.png'

import AOS from 'aos';
import 'aos/dist/aos.css'; // You can also use <link> for styles
// ..
AOS.init();


export default function Landing() {
  

  return (
      <div  className='landing-div'>
      
      <div className='landing-card'>
      <img data-aos="fade-down" data-aos-delay="800"  src={Avatar}></img>
   <p data-aos="fade-up" data-aos-delay="800">Welcome to my personal website and portfolio!</p>
   <AnchorLink href='#body-1'>
   <button data-aos="fade-up" data-aos-delay="800"    type="button" class="btn btn-primary">Click here to find out more about me</button>
</AnchorLink>
   </div>
   
    </div>
  );
}