import React from 'react'
import '../Styles/App.css'

import Logo from '../images/MD.svg'

import  {Link} from 'react-router-dom'


const Navy = () => {
  return(
    <div >
      <nav className="navbar"  class="navbar sticky-top navbar-expand-lg navbar-dark bg-primary">
  <a class="navbar-brand" href="#"> <img className='MD-logo' src={Logo}></img> </a>
  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>
  <div class="collapse navbar-collapse" id="navbarNav">
    <ul class="navbar-nav">
      <li class="nav-item active">
        <Link class="nav-link"  to="/react-personal">Home <span class="sr-only">(current)</span></Link>
        
      </li>
      <li class="nav-item active">

        <Link class="nav-link" to="/contact">Contact <span class="sr-only">(current)</span></Link>

      </li>
      
    </ul>
    
  </div>
</nav>

    </div>
  )
}

export default Navy