import React from 'react'
import '../Styles/App.css'
import {
    Card, CardImg, CardText, 
    CardTitle
  } from 'reactstrap';
import first from '../images/first.svg'
import second from '../images/second.svg'

import AOS from 'aos';
import 'aos/dist/aos.css'; // You can also use <link> for styles
// ..
AOS.init();


const Body = () => {
    return(
        <div className='body-container'>
            <div  className='body-1' id='body-1'>
            
            <div data-aos="fade-in" className='card'>
            <Card body inverse color="success">
            <CardImg  className='card-image'  top width="100%" src={first} alt="Card image cap" />
        <CardTitle className='card-title'>Designer</CardTitle>
        <CardText className='card-text'>I value beautiful and simple code that creates an amazing end-product.</CardText>
        <CardTitle className='card-title2'>Development Tools</CardTitle>
        <CardText className='card-text2'>
        <p>VS Code</p> 
        <p>CodePen</p>
        <p>Dev Tools</p>
        <p>Github</p>
</CardText>
      </Card>
</div>
            <div className='card'>  
            <Card body inverse color="info">
            <CardImg  className='card-image'  top width="100%" src={second} alt="Card image cap" />
        <CardTitle className='card-title'>Front-End Developer</CardTitle>
        <CardText className='card-text'>I like to code from scratch while also learning new methods on the fly.</CardText>
        <CardTitle className='card-title2'>Development Tools</CardTitle>
        <CardText className='card-text2'>
        <p>HTML</p> 
        <p>CSS</p>
        <p>JavaScript</p>
        <p>React</p>
        <p>SQL</p>
        <p>NodeJS</p>

</CardText>
      </Card>
      </div>

            <div  className='card'>
      <Card body inverse style={{ backgroundColor: '#333', borderColor: '#333' }}>
            <CardImg  className='card-image'  top width="100%" src={first} alt="Card image cap" />
        <CardTitle className='card-title'>Background</CardTitle>
        <CardText className='card-text'>What education do I have and how did I get to where I am today?</CardText>
        <CardTitle className='card-title2' style={{paddingTop:'20px'}}>Learning Methods</CardTitle>
        <CardText className='card-text2'>
        <p>B.S. Information Technology</p> 
        <p>Building live and responsive projects</p>
        <p>JavaScript</p>
        <p><a href="https://www.udemy.com/" target='blank'>Udemy</a></p>
        <p><a href="https://www.w3schools.com" target='blank'>W3School</a></p>

</CardText>
      </Card>
      </div>
            </div>

            <div  className='body-1' id='body-2'>
            <div data-aos="fade-in"  className='card'>
      <Card body inverse style={{ backgroundColor: '#333', borderColor: '#333' }}>
            <CardImg  className='card-image'  top width="100%" src={second} alt="Card image cap" />
        <CardTitle className='card-title'>Background</CardTitle>
        <CardText className='card-text'>What education do I have and how did I get to where I am today?</CardText>
        <CardTitle className='card-title2'>Learning Methods</CardTitle>
        <CardText className='card-text2'>
        <p>B.S. Information Technology</p> 
        <p>CSS</p>
        <p>JavaScript</p>
        <p>React</p>
        <p>SQL</p>
        <p>NodeJS</p>

</CardText>
      </Card>
      </div>
      <div data-aos="fade-in" data-aos-delay="200"  className='card'>
      <Card body inverse color="info">
            <CardImg  className='card-image'  top width="100%" src={second} alt="Card image cap" />
        <CardTitle className='card-title'>Background</CardTitle>
        <CardText className='card-text'>What education do I have and how did I get to where I am today?</CardText>
        <CardTitle className='card-title2'>Learning Methods</CardTitle>
        <CardText className='card-text2'>
        <p>B.S. Information Technology</p> 
        <p>CSS</p>
        <p>JavaScript</p>
        <p>React</p>
        <p>SQL</p>
        <p>NodeJS</p>

</CardText>
      </Card>
      </div>
      <div data-aos="fade-in" data-aos-delay="400"  className='card'>
      <Card body inverse color="success">
            <CardImg  className='card-image'  top width="100%" src={second} alt="Card image cap" />
        <CardTitle className='card-title'>Background</CardTitle>
        <CardText className='card-text'>What education do I have and how did I get to where I am today?</CardText>
        <CardTitle className='card-title2'>Learning Methods</CardTitle>
        <CardText className='card-text2'>
        <p>B.S. Information Technology</p> 
        <p>CSS</p>
        <p>JavaScript</p>
        <p>React</p>
        <p>SQL</p>
        <p>NodeJS</p>

</CardText>
      </Card>
      </div>
            </div>
        </div>
    )
}

export default Body